import {
  CHANGE_AUTH_STATUS,
  STORE_USER_DATA,
} from "./action-types";

export const initialState = {
  /**    */
  auth: false,
  /**    */
  user: {
    /**    */
    avatarUrl: ``,
    /** email */
    email: ``,
    /**  */
    id: null,
    /**   */
    isPro: null,
    /**   */
    name: ``,
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_AUTH_STATUS:
      return {
        ...state,
        auth: action.payload,
      };
    case STORE_USER_DATA:
      return {
        ...state,
        user: action.payload,
      };
    default:
      return state;
  }
};
